import React from 'react'

import EditButton from './editObjectiveButton'

import {Table} from 'react-bootstrap'
import Form from 'react-bootstrap/Form'
import {Button} from 'react-bootstrap'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import ButtonToolbar from 'react-bootstrap/ButtonToolbar'

class ObjectiveForm extends React.Component{
	render(){
		return(
			<div>
				<br />
				<h4>PI, Objectives, and BV</h4>
				<br />
				<Form name="objectiveForm" onSubmit={this.props.handleObjectiveSubmit}>
				  <Container>

				  <Row>

				  	  <Col md={5}>
						<Form.Group controlId="exampleForm.BV">
							<Form.Label>BV:</Form.Label>
						    <Form.Control as="select" name="bv" onChange={this.props.changeHandler}>
						    	<option value="0">Select BV Value</option>
						    	<option vaule="1">1</option>
						    	<option value="2">2</option>
						    	<option value="3">3</option>
						    	<option value="4">4</option>
								<option value="5">5</option>
							</Form.Control>
							{this.props.objerror && <span className="error text-danger">Must add objective and BV value with PI</span>}
						</Form.Group>
					  </Col>
				  
					  <Col>
					  	<Form.Group controlId="exampleForm.Objective">
					  		<Form.Label>Objective:</Form.Label>
    						<Form.Control as="textarea" rows="4" name="objective" placeholder="Add Objective" onChange={this.props.changeHandler}/>
  						</Form.Group>
					  </Col>

				  	  

					  
				  </Row>
				  <Row>
				  	<Col sm={{offset:10}}>
					  	<Button bsPrefix="guardianbuttonimportant" variant="outline-primary" type="submit" size="sm">
    						Submit
  						</Button>
  					</Col>
				  </Row>
				  </Container>
				</Form>

				<br />
				<br />

				<Table striped bordered hover size="sm">
				  <thead>
				    <tr>
						<th>PI</th>
						<th>BV</th>
						<th>Objective</th>
						<th>Actions</th>						
					</tr>
				  </thead>
				  <tbody>
				  	
				    {this.props.list.map((item,index)=>(
						<tr key={index}>
							<td>{item.pinc}</td>
							<td>{item.bv}</td>
							<td>{item.objective}</td>
							
							<ButtonToolbar className="actionsbar" aria-label="Toolbar with edit and remove buttons">

								<Button bsPrefix="guardianbutton spacebutton" variant="danger" size="sm" onClick={() => this.props.removeFromObjectiveTable(item.pinc)}>Remove</Button>
																		
								<EditButton
									changeHandler={this.props.changeHandler}
									editObjectiveSubmit={this.props.editObjectiveSubmit}
									list={this.props.list}
									index={index}
								/>

							</ButtonToolbar>
							
						</tr>
					))}
				  </tbody>
				</Table>
			
				<br />
				<br />

			</div>
		);
	}
}

export default ObjectiveForm;