import React from 'react';

import './App.css';
import MyHeader from './components/myHeader'
import Selection from './components/selection'
import HappyForm from './components/happyForm'
import ObjectiveForm from './components/objectiveForm'

import Chart from './components/chart'

import { TabContent, TabPane, Nav, NavItem, NavLink, Row, Col } from 'reactstrap';
import classnames from 'classnames';

class App extends React.Component {
	chartReference = {};
	constructor() {
		super()
		this.state={

			train:null,
			team:null,

			//from selection of train/team/pi
			pinc:null,
			date:null,

			//from happiness form
			happiness:null,
			description:null,
			iteration:null,
			dform: [],
			pi_iter:null,

			//from happiness edit modal
			modalhappiness:null,
			modaldescription:null,
			modaliteration:null,

			//error message variables for happiness form
			haperror:false,
			iterationerror:false,

			//objective form variables
			bv: null,
			objective: null,
			objectiveform: [],

			//objective form edit variables
			modalbv:null,
			modalobjective:null,

			//error message variables for objective form
			objerror:false,

			activeTab: '1',

			chartData:{},
		}
		this.changeHandler = this.changeHandler.bind(this)
		this.handleSubmit = this.handleSubmit.bind(this)
		this.handleObjectiveSubmit = this.handleObjectiveSubmit.bind(this)
		this.editObjectiveSubmit = this.editObjectiveSubmit.bind(this)
		this.toggle = this.toggle.bind(this)
	}

	componentWillMount(){
		this.getChartData();
	}

	getChartData(){
		//AJXA call here aka API call?
		this.setState({
			chartData: {
				labels:['Team 1','Team 2','Team 3'],
				datasets:[{
					label:'Team BVs',
					data:[
						502,
						92,
						245,
					],
					backgroundColor:[
						'rgba(255,99,132,0.6)',
						'rgba(54,162,235,0.6)',
						'rgba(255,206,86,0.6',
					]
				}]
			}
		})
	}


	toggle(tab) {
	  if (this.state.activeTab !== tab) {
	    this.setState({
	      activeTab: tab
	    });
	  }
	}

	changeHandler = (event) => {
		const {name,value} = event.target
		
		this.setState({
			[name]: value,
		}, ()=>{

			const newItem = {
			happiness:this.state.happiness,
			description:this.state.description,
			pinc:this.state.pinc,
			iteration:this.state.iteration,
			objective:this.state.objective,
			bv:this.state.bv,
			}

			//happiness 1,2,5 does not have description
			if( ( (newItem.happiness==="1")||(newItem.happiness==="2")||(newItem.happiness==="5") ) && ((newItem.description === null)||(newItem.description === "")) ){
				this.setState({haperror:true})
			}
			else{
				this.setState({haperror:false})
			}

			//no pi or iteration
			if( ((newItem.pinc==="0")||(newItem.pinc===null)) || ((newItem.iteration==="0")||(newItem.iteration===null)) ){
				this.setState({iterationerror:true})
			}
			else{
				this.setState({iterationerror:false})
			}

			//no pi or bv or objective
			if( ((newItem.pinc==="0")||(newItem.pinc===null)) || ((newItem.bv==="0")||(newItem.bv===null)) ||  ( (newItem.objective === null)||(newItem.objective === "") ) ){
				this.setState({objerror:true})
			}
			else{
				this.setState({objerror:false})
			}

			//console.log(this.state.objective)

		})
	
	}

	handleSubmit = (event) => {
		event.preventDefault()
		const newItem = {
			happiness:this.state.happiness,
			description:this.state.description,
			iteration: this.state.iteration,
			pinc: this.state.pinc,
		}

		//ensures description for happiness 1,2,5
		if( !( (newItem.happiness==="3")||(newItem.happiness==="4") ) && ((newItem.description === null)||(newItem.description === "")) ){
			console.log("error: if happiness is 1, 2, or 5 you must have a description!")
		}

		//ensures pi,iteration,and happiness value chosen
		else if( 
			((newItem.happiness==="1")||(newItem.happiness==="2")||(newItem.happiness==="3")||(newItem.happiness==="4")||(newItem.happiness==="5")) && 
			((newItem.iteration==="1")||(newItem.iteration==="2")||(newItem.iteration==="3")||(newItem.iteration==="4")||(newItem.iteration==="5")) &&
			((newItem.pinc==="1")||(newItem.pinc==="2")||(newItem.pinc==="3")||(newItem.pinc==="4")||(newItem.pinc==="5"))
			){
			const newItem1 = {
				team:this.state.team,
				train:this.state.train,
				happiness:this.state.happiness,
				description:this.state.description,
				iteration: this.state.iteration,
				pinc: this.state.pinc,
				pi_iter: this.state.pinc.concat("."+this.state.iteration)
			}
			this.setState({
				dform: this.state.dform.concat(newItem1),
			})
			console.log("submitted / concat new info to dform")
			//this.getChartData();
		}

		//all else
		else{
			console.log("ERROR")
		}
	}

	handleObjectiveSubmit = (event) => {
		event.preventDefault()
		const newItem = {
			team:this.state.team,
			train:this.state.train,
			bv:this.state.bv,
			objective:this.state.objective,
			pinc: this.state.pinc,
		}
		if( ((newItem.pinc==="0")||(newItem.pinc===null)) || ((newItem.bv==="0")||(newItem.bv===null)) ||  ( (newItem.objective === null)||(newItem.objective === "") ) ){
			console.log("error: you need a PI, BV, and objective!")
		}
		else{
			this.setState({
				objectiveform: this.state.objectiveform.concat(newItem),
			})
		}
	}

	removeFromTable = (pi_iter) => {
				this.setState({
						dform: this.state.dform.filter((forminput)=>forminput.pi_iter!==pi_iter)
				})
	}

	removeFromObjectiveTable = (pinc) => {
				this.setState({
						objectiveform: this.state.objectiveform.filter((forminput)=>forminput.pinc!==pinc)
				})
	}

	editObjectiveSubmit = (index) => {

		const newItem = {
			train:this.state.train,
			team:this.state.team,
			bv:this.state.modalbv,
			objective:this.state.modalobjective,
			pinc: this.state.pinc,
		}

		const newobjectiveform = this.state.objectiveform.slice() //copy array
		newobjectiveform[index] = newItem

		this.setState({
			objectiveform: newobjectiveform,
		})
		
	}

	editHappySubmit = (index) => {

		const newItem = {
			team:this.state.team,
			train:this.state.train,
			happiness:this.state.modalhappiness,
			iteration:this.state.modaliteration,
			description: this.state.modaldescription,
			pinc: this.state.pinc,
			pi_iter: this.state.pinc.concat("."+this.state.modaliteration)
		}

		const newdform = this.state.dform.slice() //copy array
		newdform[index] = newItem

		this.setState({
			dform: newdform,
		})
		
	}

	render(){
		return (
			<div>
				<MyHeader 
					train={this.state.train}
					team={this.state.team}
				/>

				<div className="body">

				<Selection 
					changeHandler={this.changeHandler}
				/>
				<hr />
				<br />
				
				<Nav tabs>
					<NavItem className="splittabs">
						<NavLink
							className={classnames({active:this.state.activeTab === '1'})}
							onClick={()=> {this.toggle('1');} }
						>
							Objectives
						</NavLink>
					</NavItem>

					<NavItem className="splittabs">
						<NavLink
							className={classnames({active:this.state.activeTab === '2'})}
							onClick={()=> {this.toggle('2');} }
						>
							Happiness
						</NavLink>
					</NavItem>
				</Nav>

				<TabContent activeTab={this.state.activeTab}>
					<TabPane tabId="1">
						<Row>
							<Col sm="12">
								<ObjectiveForm
									handleObjectiveSubmit={this.handleObjectiveSubmit}
									changeHandler={this.changeHandler}
									list={this.state.objectiveform}
									objerror={this.state.objerror}
									removeFromObjectiveTable={this.removeFromObjectiveTable}
									editObjectiveSubmit={this.editObjectiveSubmit}
								/>
							</Col>
						</Row>
					</TabPane>

					<TabPane tabId="2">
						<Row>
							<Col sm="12">
								<HappyForm 
									handleSubmit={this.handleSubmit}
									changeHandler={this.changeHandler}
									list={this.state.dform}
									haperror={this.state.haperror}
									iterationerror={this.state.iterationerror}
									removeFromTable={this.removeFromTable}
									editHappySubmit={this.editHappySubmit}
								/>
							</Col>
						</Row>
					</TabPane>
				</TabContent>

					<hr />
				<Chart chartData={this.state.chartData} />
				</div>
			</div>
		);
  	}
}

export default App;
