import React from 'react'
import Form from 'react-bootstrap/Form'
import {Button} from 'react-bootstrap'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'


class Selection extends React.Component{
	render(){
		return(
			<div>
				<Form>
				  <Container>
				  <Row>
				  	  <Col>
						  <Form.Group controlId="exampleForm.Train">
						    <Form.Label>Train:</Form.Label>
						    <Form.Control as="select" name="train" onChange={this.props.changeHandler}>
						    	<option value="0">Select Train</option>
						    	<option value="Train 1">Train 1</option>
						    </Form.Control>
						  </Form.Group>
					  </Col>

					  <Col>
						  <Form.Group controlId="exampleForm.Team">
						    <Form.Label>Team:</Form.Label>
						    <Form.Control as="select" name="team" onChange={this.props.changeHandler}>
						    	<option value="0">Select Team</option>
						    	<option value="Team 1">Team 1</option>
						    	<option value="Team 2">Team 2</option>
						    	<option value="Team 3">Team 3</option>
						    	<option value="Team 4">Team 4</option>
						    </Form.Control>
						  </Form.Group>
					  </Col>

					  <Col>
						  <Form.Group controlId="exampleForm.PI">
						    <Form.Label>PI:</Form.Label>
						    <Form.Control as="select" name="pinc" onChange={this.props.changeHandler}>
						    	<option value="0">Select PI</option>
						    	<option value="1">1</option>
						    	<option value="2">2</option>
						    	<option value="3">3</option>
						    	<option value="4">4</option>
						    	<option value="5">5</option>
						    </Form.Control>
						  </Form.Group>
					  </Col>
					  
				  </Row>
				  	
				  <Row>
				    <Col md={{offset:10}}>
					  	{/*<Button bsPrefix="guardianbutton" variant="outline-primary" type="submit" size="sm">
    						Search
  						</Button>*/}
  					</Col>
				  </Row>
				  </Container>
				</Form>

			</div>
		);
	}
}

export default Selection;