import React from 'react'

import Navbar from 'react-bootstrap/Navbar'
//import Nav from 'react-bootstrap/Nav'

//import GuardianLogo from '../guardian-logo.svg'


class MyHeader extends React.Component{
	render(){
		return(
			<div>


			  <Navbar>

			    <Navbar.Brand href="#home">
				  	<img
				  		alt=""
				  		src=""//{GuardianLogo}
				  		width="160px"
				  		height="30px"
				  		className="d-inline-block align-top"
				  	/>
			  	</Navbar.Brand>


			  	<Navbar.Collapse className="justify-content-end">
				    
				    {(this.props.train !== "0") && (this.props.team !== "0") && this.props.train && this.props.team && <span>Welcome {this.props.team}  (Train: {this.props.train})</span>}
				    
				</Navbar.Collapse>



			  	{/*
			    <Nav className="mr-auto">
			       <Nav.Link href="#home">Home</Nav.Link>
			    </Nav>
				*/}
			    
			  </Navbar>

			  <Navbar className="mainnav" variant="dark">

			    <Navbar.Brand href="#home">	Navbar </Navbar.Brand>
			    
			  </Navbar>			  

			  <br />
		

			</div>
		);
	}
}

export default MyHeader;