import React from 'react'

import EditButton from './editHappyButton'

import {Table} from 'react-bootstrap'
import Form from 'react-bootstrap/Form'
import {Button} from 'react-bootstrap'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import ButtonToolbar from 'react-bootstrap/ButtonToolbar'


class HappyForm extends React.Component{
	render(){
		return(
			<div>
				<br />
				<h4>Iteration, Happiness, and Description</h4>
				<br />
				<Form name="happyForm" onSubmit={this.props.handleSubmit}>
				  <Container>
				  <Row>
				  	  <Col md={2}>
						<Form.Group controlId="exampleForm.Iteration">
							<Form.Label>Iteration:</Form.Label>
						    <Form.Control as="select" name="iteration" onChange={this.props.changeHandler}>
						    	<option value="0">Select Iteration</option>
						    	<option value="1">1</option>
						    	<option value="2">2</option>
						    	<option value="3">3</option>
						    	<option value="4">4</option>
								<option value="5">5</option>
							</Form.Control>
							{this.props.iterationerror && <span className="error text-danger">Must add PI and iteration</span>}
						</Form.Group>

					  </Col>

				  	  <Col md={3}>
						<Form.Group controlId="exampleForm.Happiness">
							<Form.Label>Happiness:</Form.Label>
						    <Form.Control as="select" name="happiness" onChange={this.props.changeHandler}>
						    	<option value="0">Select Happiness Value</option>
						    	<option value="1">1</option>
						    	<option value="2">2</option>
						    	<option value="3">3</option>
						    	<option value="4">4</option>
								<option value="5">5</option>
							</Form.Control>
							{this.props.haperror && <span className="error text-danger">Must add description if happiness is 1, 2, or 5</span>}
						</Form.Group>

					  </Col>

					  <Col>
					  	<Form.Group controlId="exampleForm.Description">
					  		<Form.Label>Description:</Form.Label>
    						<Form.Control as="textarea" rows="4" name="description" placeholder="Add Description" onChange={this.props.changeHandler}/>
  						</Form.Group>
					  </Col>
					  
				  </Row>
				  <Row>

				  	<Col sm={{offset:10}}>
					  	<Button bsPrefix="guardianbuttonimportant" variant="outline-primary" type="submit" size="sm">
    						Submit
  						</Button>
  					</Col>
				  </Row>
				  </Container>
				</Form>

				<br />
				<br />

				<Table striped bordered hover size="sm">
				  <thead>
				    <tr>
				    	<th>Iteration</th>
						<th>Happiness</th>
						<th>Description</th>
						<th>Actions</th>
					</tr>
				  </thead>
				  <tbody>
				  	
				    {this.props.list.map((item,index)=>(
						<tr key={item.pi_iter}>
							<td>{item.pi_iter}</td>
							<td>{item.happiness}</td>
							<td>{item.description}</td>
							<ButtonToolbar className="actionsbar" aria-label="Toolbar with edit and remove buttons">
								<Button bsPrefix="guardianbutton spacebutton" variant="danger" size="sm" onClick={() => this.props.removeFromTable(item.pi_iter)}>Remove</Button>
								

								<EditButton 
									changeHandler={this.props.changeHandler}
									editHappySubmit={this.props.editHappySubmit}
									list={this.props.list}
									index={index}
								/>
								
							</ButtonToolbar>
						</tr>
					))}
				  </tbody>
				</Table>
			
				<br />
				<br />


			</div>
		);
	}
}

export default HappyForm;