import React from 'react'

import Form from 'react-bootstrap/Form'
import {Button} from 'react-bootstrap'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import Modal from 'react-bootstrap/Modal'


class EditButton extends React.Component{
  constructor(){
    super()
    this.state={
      show:false,
    }
  //const [show, setShow] = useState(false);

    this.handleClose = this.handleClose.bind(this)
    this.handleShow = this.handleShow.bind(this)
  }

  //const handleClose = () => setShow(false);
  handleClose(){
    this.setState({
      show:false,
    })
  }

  //  const handleShow = () => setShow(true);
  handleShow(){
    this.setState({
      show:true,
    })
  }

  //prevent refresh
  refreshPrevent(event){
    event.preventDefault()
  }

  wrapperfunction(index){
    this.props.editHappySubmit(index)
    this.handleClose()
    //console.log(this.props.list)
  }

  render(){
    return (
      <div>
        <Button bsPrefix="guardianbutton spacebutton" variant="primary" onClick={this.handleShow} size="sm">
          Edit
        </Button>

        <Modal 
          show={this.state.show} onHide={this.handleClose}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title>Edit Iteration, Happiness, and Description</Modal.Title>
          </Modal.Header>
          <Modal.Body>

          <Form name="happyFormModal" onSubmit={this.refreshPrevent}>
            <Container>
            <Row>
                <Col md={2}>
              <Form.Group controlId="exampleForm.IterationModal">
                <Form.Label>Iteration:</Form.Label>
                  <Form.Control as="select" name="modaliteration" onChange={this.props.changeHandler}>
                    <option value="0">Select Iteration</option>
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                  <option value="5">5</option>
                </Form.Control>
                {this.props.iterationerror && <span className="error text-danger">Must add PI and iteration</span>}
              </Form.Group>

              </Col>

                <Col md={3}>
              <Form.Group controlId="exampleForm.HappinessModal">
                <Form.Label>Happiness:</Form.Label>
                  <Form.Control as="select" name="modalhappiness" onChange={this.props.changeHandler}>
                    <option value="0">Select Happiness Value</option>
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                  <option value="5">5</option>
                </Form.Control>
                {this.props.haperror && <span className="error text-danger">Must add description if happiness is 1, 2, or 5</span>}
              </Form.Group>

              </Col>

              <Col>
                <Form.Group controlId="exampleForm.DescriptionModal">
                  <Form.Label>Description:</Form.Label>
                  <Form.Control as="textarea" rows="4" name="modaldescription" placeholder="Add Description" onChange={this.props.changeHandler}/>
                </Form.Group>
              </Col>
              
            </Row>
            </Container>
          </Form>
          
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleClose}>
              Close
            </Button>
            <Button variant="primary" onClick={() => this.wrapperfunction(this.props.index) }>
              Save Changes
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }

}


export default EditButton;